<template>
    <div class="fullpage-container" ref='fullpage' :style="styleObj">
            <div class="scroll-container" v-show='isShow' ref='scrollContainer' style="transition: all ease 1s;">
                <slot name='section'></slot>  
            </div>
        </div>
</template>
<script>
export default {
    data(){
        return{
            fullpage:{
                current:1,
                isScrolling: false,
                // è¿åé¼ æ æ»è½®çåç´æ»å¨é
                deltaY:0,
                styleObj:{
                  position: 'absolute',
                  overflow: 'hidden',
                  top: 0,
                  bottom: 0,
                  left: 0,
                  right: 0,
                }
            },
            isShow:false,
        }
    },
    mounted() {
        this.initFullPage()
        // æ»å¨äºä»¶ç»å®
        document.addEventListener('mousewheel',this.mouseWheelHandle)
        //çªå£resizeæ¶åéæ°è®¾è®¡å¤§å°
        window.addEventListener('resize',this.resizeEventHandler)

    },
    beforeDestroy() {
        // æ»å¨äºä»¶è§£ç»
        document.removeEventListener('mousewheel',this.mouseWheelHandle)
        window.removeEventListener("resize", this.resizeEventHandler, false);
    },
    methods:{
        resizeEventHandler(){
            //èæµ,èèæç
            throttle(this.initFullPage(),300)
        },
        initFullPage(){
            //åå§åå®¹å¨é«åº¦
            let height = this.$refs['fullpage'].clientHeight;
            this.isShow=false
            //æå¨è®¾ç½®slotséé¢ä¸ºsectionç
            this.$slots.section.forEach((item)=>{
                item.elm.style.cssText=`height:${height}px;`
            })
            //æ¾ç¤ºæ»å¨çå­
            this.isShow=true
        },
        next() {
            let len = this.$slots.section.length;
            if((this.fullpage.current + 1) <= len) {
                this.fullpage.current += 1;
                this.move(this.fullpage.current);
            }
        },
        pre() {
            if(this.fullpage.current -1 > 0) {
                this.fullpage.current -= 1;
                this.move(this.fullpage.current);
            }
        },
        move(index) {
            // ä¸ºäºé²æ­¢æ»å¨å¤æ¬¡æ»å¨ï¼éè¦éè¿ä¸ä¸ªåéæ¥æ§å¶æ¯å¦æ»å¨
            let height = this.$refs['fullpage'].clientHeight;
            let $scroll = this.$refs['scrollContainer'];
            this.fullpage.isScrolling = true;
            let di = -(index-1)*height + 'px';
            $scroll.style.cssText=`transform:translateY(${di})`
            this.$emit('leaveSlide',{currentIndex:this.fullpage.current})
            //è¿éçå¨ç»æ¯1sæ§è¡å®ï¼ä½¿ç¨setTimeoutå»¶è¿1såè§£é
            setTimeout(()=>{
                this.fullpage.isScrolling = false;
            }, 1010);
        },
        mouseWheelHandle (event) {
            if (this.fullpage.isScrolling) {// å éé¨å
                return false;
            }
            let e = event.originalEvent || event;
            this.fullpage.deltaY = e.deltaY;
            if (this.fullpage.deltaY > 0) {
                this.next();
            } else if (this.fullpage.deltaY < 0) {
                this.pre();
            }
        },
        setAllowScrolling(isAllow){
            if(isAllow){
                document.addEventListener('mousewheel',this.mouseWheelHandle)
            }else{
                document.removeEventListener('mousewheel',this.mouseWheelHandle)
            }
        }
    }
}
function throttle(fn, delay) {
  var timer = null;
  return function() {
    var context = this,
      args = arguments;
    clearTimeout(timer);
    timer = setTimeout(function() {
      fn.apply(context, args);
    }, delay);
  };
}

</script>
<style  scoped>
.fullpage {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.scrollContainer {
  transition: all ease 1s;
}
</style>